import React, { useEffect, useState } from "react";
import { Layout } from "../../components/admin/Layouts";
import axios from "axios";
import { Select } from "antd";
import { useNavigate, useParams } from "react-router-dom";

const { Option } = Select;

export const UpdatePost = () => {
  const navigate = useNavigate();
  const params = useParams();
  const [categories, setCategories] = useState([]);
  const [title, setTitle] = useState("");
  const [category, setCategory] = useState("");
  const [photo, setPhoto] = useState("");
  const [location, setLocation] = useState("");
  const [price, setPrice] = useState("");
  const [description, setDescription] = useState("");
  const [id, setId] = useState("");

  useEffect(() => {
    const getAllCategories = async () => {
      try {
        const { data } = await axios.get("/api/v1/auth/all-category");
        if (data?.success) {
          setCategories(data?.category);
        }
      } catch (error) {
        console.log("Error fetching categories:", error);
      }
    };

    getAllCategories();
  }, []);

  useEffect(() => {
    const getSinglePost = async () => {
      try {
        const { data } = await axios.get(
          `/api/v1/auth/get-singlePost/${params.slug}`
        );
        setDescription(data?.posts?.description || "");
        setId(data?.posts?._id || "");
        setTitle(data?.posts?.title || "");
        setCategory(data?.posts?.category._id || "");
        setPrice(data?.posts?.price || "");
        setLocation(data?.posts?.location || "");
        setPhoto(data?.posts?.photo || "");
        console.log(data);
      } catch (error) {
        console.log("Error fetching single post:", error);
      }
    };

    getSinglePost();
  }, [params.slug]);

  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      const formData = new FormData();
      formData.append("title", title);
      formData.append("category", category);
      formData.append("location", location);
      formData.append("price", price);
      formData.append("description", description);
      photo && formData.append("photo", photo);

      const res = await axios.put(`/api/v1/auth/update-post/${id}`, formData);
      if (res && res.data.success) {
        console.log(res.data.message);
        navigate("/all-post");
      }
    } catch (error) {
      console.log("Error updating post:", error);
    }
  };

  return (
    <Layout>
      <div className="content-wrapper">
        <section className="content-header">
          <div className="container-fluid">
            <div className="row mb-2">
              <div className="col-sm-6">
                <h1>Edit Property</h1>
              </div>
              <div className="col-sm-6">
                <ol className="breadcrumb float-sm-right">
                  <li className="breadcrumb-item">
                    <a href="#">Home</a>
                  </li>
                </ol>
              </div>
            </div>
          </div>
        </section>

        <section className="content">
          <div className="card">
            <div className="card-body">
              <div className="card card-primary">
                <div className="card-header">
                  <h3 className="card-title">Edit Property</h3>
                </div>
                <form onSubmit={handleSubmit}>
                  <div className="card-body">
                    <div className="form-group">
                      <label htmlFor="title">Title</label>
                      <input
                        type="text"
                        className="form-control"
                        id="title"
                        name="title"
                        value={title}
                        onChange={(e) => setTitle(e.target.value)}
                        placeholder="Enter title"
                      />
                    </div>

                    <div className="form-group">
                      <label htmlFor="location">Location</label>
                      <input
                        type="text"
                        className="form-control"
                        id="location"
                        name="location"
                        value={location}
                        onChange={(e) => setLocation(e.target.value)}
                        placeholder="Enter Location"
                      />
                    </div>

                    <div className="form-group">
                      <label htmlFor="price">Price</label>
                      <input
                        type="number"
                        className="form-control"
                        id="price"
                        name="price"
                        value={price}
                        onChange={(e) => setPrice(e.target.value)}
                        placeholder="Enter Price"
                      />
                    </div>

                    <Select
                      placeholder="Select category"
                      onChange={(value) => {
                        setCategory(value);
                      }}
                      value={category}
                    >
                      {categories?.map((c) => (
                        <Option key={c._id} value={c._id}>
                          {c.name}
                        </Option>
                      ))}
                    </Select>

                    <div className="mb-4">
                      <label className="btn btn-outline-primary col-md-12">
                        {photo ? photo.title : "Upload Image"}
                        <input
                          type="file"
                          name="photo"
                          accept="image/*"
                          onChange={(e) => setPhoto(e.target.files[0])}
                          hidden
                        />
                      </label>
                    </div>
                    {photo ? (
                      <div className="text-center">
                        <img
                          src={URL.createObjectURL(photo)}
                          alt="post_photo"
                          height={"200px"}
                          className="img img-responsive"
                        />
                      </div>
                    ) : (
                      <div className="text-center">
                        <img
                          src={`/api/v1/auth/get-photo/${id}`}
                          alt="post_photo"
                          height={"200px"}
                          className="img img-responsive"
                        />
                      </div>
                    )}

                    <div className="form-group">
                      <label htmlFor="description">Property Description</label>
                      <textarea
                        className="form-control"
                        rows="3"
                        id="description"
                        name="description"
                        value={description}
                        onChange={(e) => setDescription(e.target.value)}
                      ></textarea>
                    </div>
                  </div>

                  <div className="card-footer">
                    <button type="submit" className="btn btn-primary btn-block">
                      Update Property
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  );
};
