import React, { useEffect, useState } from "react";
import { Layout } from "../../components/admin/Layouts";
import axios from "axios";
import { Modal } from "antd";
import { PostCategoryForm } from "../../components/form/form";
import { Link } from "react-router-dom";
import { useAuth } from "../../context/auth";
export const AllproPerty = () => {
  const [posts, setPosts] = useState([]);
  const [Userposts, setUserPosts] = useState([]); 
  const [auth, setAuth] = useAuth();
  useEffect(() => {
    getAllPosts();
  }, []);
  const getAllPosts = async () => {
    try {
      const { data } = await axios.get("/api/v1/auth/all-post");
      if (data.success) {
        setPosts(data.posts);
      }
    } catch (error) {
      console.log(error);
    }
  };
  const getPost = async () => { 
    try {
      const { data } = await axios.get("/api/v1/auth/get-userData");
      setUserPosts(data); 
      //console.log(data) 
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    if (auth?.token) getPost();
  }, [auth?.token]);

  const handleDelete = async (postId) => {
    try {
      const { data } = await axios.delete(`/api/v1/auth/delete-post/${postId}`);
      if (data.success) {
        alert("Post deleted successfully");
        getAllPosts(); 
      }
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <Layout>
      <div className="content-wrapper">
        <section className="content-header">
          <div className="container-fluid">
            <div className="row mb-2">
              <div className="col-sm-6">
                <h1>All My Property</h1>
              </div>
              <div className="col-sm-6">
                <ol className="breadcrumb float-sm-right">
                  <li className="breadcrumb-item">
                    
                  </li>
                  <li className="breadcrumb-item active">All My Property</li>
                </ol>
              </div>
            </div>
          </div>
        </section>
        <section className="content">
          <div className="card">
            <div className="card-body">
              <table
                id="example1"
                className="table table-bordered table-striped"
              >
                <thead>
                  <tr>
                    <th>Property Title</th>
                    <th>Location</th>
                    <th>Price</th>
                    <th>Image</th>
                    <th>DateTime</th>
                    <th>Edit</th>
                    <th>Delete</th>
                  </tr>
                </thead>
                <tbody>
                  {Userposts.map((post) => (
                    <tr key={post._id}>
                      <td>{post.title}</td>
                      <td>{post.location}</td>
                      <td>{post.price}</td>
                      <td>
                        <img
                          src={`/api/v1/auth/get-photo/${post._id}`}
                          alt={post.title}
                          className="card-img-top"
                        />
                      </td>
                      <td>{post.createdAt.substring(0, 10)}</td>
                      <td>
                        <Link
                          to={`/updatepost/${post.slug}`}
                          className="btn btn-primary"
                        >
                          Edit
                        </Link>
                      </td>
                      <td>
                        <button
                          className="btn btn-danger"
                          onClick={() => handleDelete(post._id)}
                        >
                          Delete
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  );
};
