import React from "react";
import { Layout } from "../components/layouts/Layout";

export const Contact = () => {
    return (
        <Layout title={'Contact'}>
            <div className="container-xxl py-5">
                <div className="container">
                    <div className="text-center mx-auto mb-5 wow fadeInUp" data-wow-delay="0.1s" style={{ maxWidth: "600px" }}>
                        <h1 className="mb-3">Contact Us</h1>
                        <p>For all Inquiries please contact us.</p>
                    </div>
                    <div className="row g-4">
                        <div className="col-12">
                            <div className="row gy-4">
                                <div className="col-md-6 col-lg-4 wow fadeIn" data-wow-delay="0.1s">
                                    <div className="bg-light rounded p-3">
                                        <div className="d-flex align-items-center bg-white rounded p-3" style={{ border: "1px dashed rgba(0, 185, 142, .3)" }}>
                                            <div className="icon me-3" style={{ width: "45px", height: "45px" }}>
                                                <i className="fa fa-map-marker-alt text-primary"></i>
                                            </div>
                                            <span> Mbezi Louis, Dar es Salaam</span>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6 col-lg-4 wow fadeIn" data-wow-delay="0.3s">
                                    <div className="bg-light rounded p-3">
                                        <div className="d-flex align-items-center bg-white rounded p-3" style={{ border: "1px dashed rgba(0, 185, 142, .3)" }}>
                                            <div className="icon me-3" style={{ width: "45px", height: "45px" }}>
                                                <i className="fa fa-envelope-open text-primary"></i>
                                            </div>
                                            <span>nitafutie@gmail.com</span>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6 col-lg-4 wow fadeIn" data-wow-delay="0.5s">
                                    <div className="bg-light rounded p-3">
                                        <div className="d-flex align-items-center bg-white rounded p-3" style={{ border: "1px dashed rgba(0, 185, 142, .3)" }}>
                                            <div className="icon me-3" style={{ width: "45px", height: "45px" }}>
                                                <i className="fa fa-phone-alt text-primary"></i>
                                            </div>
                                            <span>+255678650509</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="wow fadeInUp" data-wow-delay="0.5s">
                                <div className="alert alert-danger">
                                    {/* Error message placeholder */}
                                </div>
                                <p className="mb-4">You can Tell us Your Opinion Here</p>
                                <form>
                                    <div className="row g-3">
                                        <div className="col-md-6">
                                            <div className="form-floating">
                                                <input type="text" className="form-control" id="name" placeholder="Your Name" />
                                                <label htmlFor="name">Your Name</label>
                                            </div>
                                        </div>
                                        <div className="col-md-6">
                                            <div className="form-floating">
                                                <input type="email" className="form-control" id="email" placeholder="Your Email" />
                                                <label htmlFor="email">Your Email</label>
                                            </div>
                                        </div>
                                        <div className="col-12">
                                            <div className="form-floating">
                                                <input type="text" className="form-control" id="subject" placeholder="Subject" />
                                                <label htmlFor="subject">Subject</label>
                                            </div>
                                        </div>
                                        <div className="col-12">
                                            <div className="form-floating">
                                                <textarea className="form-control" placeholder="Leave a message here" id="message" style={{ height: "150px" }}></textarea>
                                                <label htmlFor="message">Message</label>
                                            </div>
                                        </div>
                                        <div className="col-12">
                                            <button className="btn btn-primary w-100 py-3" type="submit">Send Message</button>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    )
}
