import React, { useEffect, useState } from "react";
import { Layout } from "../components/layouts/Layout";
import axios from "axios";
import { Link } from "react-router-dom";
import { useSearchContext } from "../context/search";
import { useNavigate } from "react-router-dom";
export const Home = () => {
  const navigate = useNavigate();
  const [posts, setPosts] = useState([]);
  const [values, setValues] = useSearchContext();
  const [categories, setCategories] = useState([]);
  const [checked, setChecked] = useState([]);
  const [radio, setRadio] = useState([]);
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);
  const [plot, setPlot] = useState([]);
  const [house, setHousePost] = useState([]);
  const [frame, setFrame] = useState([]);

  const getPlotPostList = async () => {
    try {
      const { data } = await axios.get("/api/v1/auth/get-Plot/");
      if (data.success) {
        setPlot(data?.posts);
      }
    } catch (error) {
      console.log(error);
    }
  };
 // get House
  const getHousePostList = async () => {
    try {
      const { data } = await axios.get("/api/v1/auth/get-house/");
      if (data.success) {
        setHousePost(data?.posts);
      }
    } catch (error) {
      console.log(error);
    }
  };

  // frameList

  const getFrametList = async () => {
    try {
      const { data } = await axios.get("/api/v1/auth/get-frame/");
      if (data.success) {
        setFrame(data?.posts);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getAllCategories();
    getTotal();
    getPlotPostList();
    getHousePostList();
    getFrametList();
  }, []);

  const getAllCategories = async () => {
    try {
      const { data } = await axios.get("/api/v1/auth/all-category");
      if (data?.success) {
        setCategories(data?.category);
      }
    } catch (error) {
      console.log("Error fetching categories:", error);
    }
  };

  const getTotal = async () => {
    try {
      const { data } = await axios.get("/api/v1/auth/post-count");
      if (data.success) {
        setTotal(data?.count);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getAllPosts();
  }, []);

  const getAllPosts = async () => {
    try {
      setLoading(true);
      const { data } = await axios.get(`/api/v1/auth/get-page/${page}`);
      setLoading(false);
      if (data.success) {
        setPosts(data.posts);
      }
    } catch (error) {
      setLoading(false);
      console.error("Error fetching posts:", error);
    }
  };

  const handleFilter = (value, id) => {
    let all = [...checked];
    if (value) {
      all.push(id);
    } else {
      all = all.filter((c) => c !== id);
    }
    setChecked([...all]);
  };

  useEffect(() => {
    if (!checked.length || radio.length) getAllPosts();
  }, [checked.length, radio.length]);

  useEffect(() => {
    if (checked.length || radio.length) searchPost();
  }, [checked, radio]);

  const searchPost = async () => {
    try {
      const { data } = await axios.post("/api/v1/auth/post-filters", {
        checked,
        radio,
      });
      if (data.success) {
        setPosts(data.posts);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleRadioChange = (e) => {
    setRadio(e.target.value);
  };

  const handleLoadMore = async () => {
    setLoading(true);
    try {
      const { data } = await axios.get(
        `/api/v1/auth/all-post?page=${page + 1}`
      );
      if (data.success) {
        setPosts([...posts, ...data.posts]);
        setPage(page + 1);
      }
    } catch (error) {
      console.error("Error loading more posts:", error);
    }
    setLoading(false);
  };

  useEffect(() => {
    if (page === 1) return;
    loadMore();
  }, [page]);

  const loadMore = async () => {
    try {
      setLoading(true);
      const { data } = await axios.get(`/api/v1/auth/get-page/${page}`);
      setLoading(false);
      setPosts([...posts, ...data?.posts]);
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.get(`/api/v1/auth/search/${values.keyword}`);
      setValues({ ...values, results: data });
      console.log(data);
      navigate("/search");
    } catch (error) {
      console.log(error);
    }
  };

  const formatNumber = (number) => {
    return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  };

  return (
    <Layout title="Nitafutie">
      <div className="container-xxl bg-white p-0">
        {/* Search */}
        <div
          className="container-fluid bg-primary mb-5 wow fadeIn"
          data-wow-delay="0.1s"
          style={{ padding: "35px", marginTop: "100px" }}
        >
          <div className="container">
            <div className="row g-2">
              <div className="col-md-10">
                <div className="row g-2">
                  <form onSubmit={handleSubmit}>
                    <div className="col-md-4">
                      <input
                        type="text"
                        className="form-control border-0 py-3"
                        placeholder="Search by location"
                        value={values.keyword}
                        onChange={(e) =>
                          setValues({ ...values, keyword: e.target.value })
                        }
                      />
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* Property Listing */}
        <div className="container-xxl py-5">
          <div className="container">
            <div className="row g-0 gx-5 align-items-end">
              <div className="col-lg-6">
                <div
                  className="text-start mx-auto mb-5 wow slideInLeft"
                  data-wow-delay="0.1s"
                >
                  <h1 className="mb-3 ">Plot Listing</h1>
                </div>
              </div>
            </div>
            <div className="tab-content">
              <div id="tab-1" className="tab-pane fade show p-0 active">
                <div className="row g-4">
                  {plot.map((post) => (
                    <div
                      key={post.id}
                      className="col-lg-4 col-md-6 wow fadeInUp"
                      data-wow-delay="0.1s"
                    >
                      <div className="property-item rounded overflow-hidden">
                        <div className="position-relative overflow-hidden">
                          <Link to={`/singlePost/${post.slug}`}>
                            <img
                              className="img-fluid"
                              src={`/api/v1/auth/get-photo/${post._id}`}
                              alt="Property"
                            />
                          </Link>
                          <div className="bg-primary rounded text-white position-absolute start-0 top-0 m-4 py-1 px-3">
                            {/* Whatever goes here */}
                          </div>
                          <div className="bg-white rounded-top text-primary position-absolute start-0 bottom-0 mx-4 pt-1 px-3">
                            {/* {posts?.category.name} */}
                          </div>
                        </div>
                        <div className="p-4 pb-0">
                          <h5 className="text-primary mb-3">
                            Tsh{formatNumber(post.price)}
                          </h5>
                          <a className="d-block h5 mb-2" href="#">
                            {post.title}
                          </a>
                          <p>
                            <i className="fa fa-map-marker-alt text-primary me-2"></i>
                            {post.location}
                          </p>
                        </div>
                        <div className="d-flex border-top">
                          <small className="flex-fill text-center border-end py-2">
                            <i className="fa fa-ruler-combined text-primary me-2"></i>
                            {post.size} Sqft
                          </small>
                          <small className="flex-fill text-center border-end py-2">
                            <i className="fa fa-bed text-primary me-2"></i>
                            {post.bedrooms} Bed
                          </small>
                          <small className="flex-fill text-center py-2">
                            <i className="fa fa-bath text-primary me-2"></i>
                            {post.bathrooms} Bath
                          </small>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>

            {/* houste */}
          </div>
        </div>

        {/* House */}

        <div className="container-xxl py-5">
          <div className="container">
            <div className="row g-0 gx-5 align-items-end">
              <div className="col-lg-6">
                <div
                  className="text-start mx-auto mb-5 wow slideInLeft"
                  data-wow-delay="0.1s"
                >
                  <h1 className="mb-3 ">House Listing</h1>
                </div>
              </div>
            </div>
            <div className="tab-content">
              <div id="tab-1" className="tab-pane fade show p-0 active">
                <div className="row g-4">
                  {house.map((post) => (
                    <div
                      key={post.id}
                      className="col-lg-4 col-md-6 wow fadeInUp"
                      data-wow-delay="0.1s"
                    >
                      <div className="property-item rounded overflow-hidden">
                        <div className="position-relative overflow-hidden">
                          <Link to={`/singlePost/${post.slug}`}>
                            <img
                              className="img-fluid"
                              src={`/api/v1/auth/get-photo/${post._id}`}
                              alt="Property"
                            />
                          </Link>
                          <div className="bg-primary rounded text-white position-absolute start-0 top-0 m-4 py-1 px-3">
                            {/* Whatever goes here */}
                          </div>
                          <div className="bg-white rounded-top text-primary position-absolute start-0 bottom-0 mx-4 pt-1 px-3">
                            {/* {posts?.category.name} */}
                          </div>
                        </div>
                        <div className="p-4 pb-0">
                          <h5 className="text-primary mb-3">
                            Tsh{formatNumber(post.price)}
                          </h5>
                          <a className="d-block h5 mb-2" href="#">
                            {post.title}
                          </a>
                          <p>
                            <i className="fa fa-map-marker-alt text-primary me-2"></i>
                            {post.location}
                          </p>
                        </div>
                        <div className="d-flex border-top">
                          <small className="flex-fill text-center border-end py-2">
                            <i className="fa fa-ruler-combined text-primary me-2"></i>
                            {post.size} Sqft
                          </small>
                          <small className="flex-fill text-center border-end py-2">
                            <i className="fa fa-bed text-primary me-2"></i>
                            {post.bedrooms} Bed
                          </small>
                          <small className="flex-fill text-center py-2">
                            <i className="fa fa-bath text-primary me-2"></i>
                            {post.bathrooms} Bath
                          </small>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>

            {/* houste */}
          </div>
        </div>
        {/* frame */}
        <div className="container-xxl py-5">
          <div className="container">
            <div className="row g-0 gx-5 align-items-end">
              <div className="col-lg-6">
                <div
                  className="text-start mx-auto mb-5 wow slideInLeft"
                  data-wow-delay="0.1s"
                >
                  <h1 className="mb-3 ">Frame Listing</h1>
                </div>
              </div>
            </div>
            <div className="tab-content">
              <div id="tab-1" className="tab-pane fade show p-0 active">
                <div className="row g-4">
                  {frame.map((post) => (
                    <div
                      key={post.id}
                      className="col-lg-4 col-md-6 wow fadeInUp"
                      data-wow-delay="0.1s"
                    >
                      <div className="property-item rounded overflow-hidden">
                        <div className="position-relative overflow-hidden">
                          <Link to={`/singlePost/${post.slug}`}>
                            <img
                              className="img-fluid"
                              src={`/api/v1/auth/get-photo/${post._id}`}
                              alt="Property"
                            />
                          </Link>
                          <div className="bg-primary rounded text-white position-absolute start-0 top-0 m-4 py-1 px-3">
                            {/* Whatever goes here */}
                          </div>
                          <div className="bg-white rounded-top text-primary position-absolute start-0 bottom-0 mx-4 pt-1 px-3">
                            {/* {posts?.category.name} */}
                          </div>
                        </div>
                        <div className="p-4 pb-0">
                          <h5 className="text-primary mb-3">
                            Tsh{formatNumber(post.price)}
                          </h5>
                          <a className="d-block h5 mb-2" href="#">
                            {post.title}
                          </a>
                          <p>
                            <i className="fa fa-map-marker-alt text-primary me-2"></i>
                            {post.location}
                          </p>
                        </div>
                        <div className="d-flex border-top">
                          <small className="flex-fill text-center border-end py-2">
                            <i className="fa fa-ruler-combined text-primary me-2"></i>
                            {post.size} Sqft
                          </small>
                          <small className="flex-fill text-center border-end py-2">
                            <i className="fa fa-bed text-primary me-2"></i>
                            {post.bedrooms} Bed
                          </small>
                          <small className="flex-fill text-center py-2">
                            <i className="fa fa-bath text-primary me-2"></i>
                            {post.bathrooms} Bath
                          </small>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>

            {/* houste */}
          </div>
        </div>
        {/* House Rent */}

        <div className="container-xxl py-5">
          <div className="container">
            <div className="row g-0 gx-5 align-items-end">
              <div className="col-lg-6">
                <div
                  className="text-start mx-auto mb-5 wow slideInLeft"
                  data-wow-delay="0.1s"
                >
                  <h1 className="mb-3 ">House Rent Listing</h1>
                </div>
              </div>
            </div>
            <div className="tab-content">
              <div id="tab-1" className="tab-pane fade show p-0 active">
                <div className="row g-4">
                  {plot.map((post) => (
                    <div
                      key={post.id}
                      className="col-lg-4 col-md-6 wow fadeInUp"
                      data-wow-delay="0.1s"
                    >
                      <div className="property-item rounded overflow-hidden">
                        <div className="position-relative overflow-hidden">
                          <Link to={`/singlePost/${post.slug}`}>
                            <img
                              className="img-fluid"
                              src={`/api/v1/auth/get-photo/${post._id}`}
                              alt="Property"
                            />
                          </Link>
                          <div className="bg-primary rounded text-white position-absolute start-0 top-0 m-4 py-1 px-3">
                            {/* Whatever goes here */}
                          </div>
                          <div className="bg-white rounded-top text-primary position-absolute start-0 bottom-0 mx-4 pt-1 px-3">
                            {/* {posts?.category.name} */}
                          </div>
                        </div>
                        <div className="p-4 pb-0">
                          <h5 className="text-primary mb-3">
                            Tsh{formatNumber(post.price)}
                          </h5>
                          <a className="d-block h5 mb-2" href="#">
                            {post.title}
                          </a>
                          <p>
                            <i className="fa fa-map-marker-alt text-primary me-2"></i>
                            {post.location}
                          </p>
                        </div>
                        <div className="d-flex border-top">
                          <small className="flex-fill text-center border-end py-2">
                            <i className="fa fa-ruler-combined text-primary me-2"></i>
                            {post.size} Sqft
                          </small>
                          <small className="flex-fill text-center border-end py-2">
                            <i className="fa fa-bed text-primary me-2"></i>
                            {post.bedrooms} Bed
                          </small>
                          <small className="flex-fill text-center py-2">
                            <i className="fa fa-bath text-primary me-2"></i>
                            {post.bathrooms} Bath
                          </small>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>

            {/* houste */}
          </div>
        </div>
      </div>
    </Layout>
  );
};
