import React from "react";
import { Layout } from "../components/layouts/Layout";

export const About = () => {
    return (
        <Layout title={'About'}>
            <div className="container-xxl py-5">
                <div className="container">
                    <div className="bg-light rounded p-3">
                        <div className="bg-white rounded p-4" style={{ border: "1px dashed rgba(0, 185, 142, .3)" }}>
                            <div className="row g-5 align-items-center">
                                <div className="col-lg-6 wow fadeIn" data-wow-delay="0.1s">
                                    <img className="img-fluid rounded w-100" src="img/call-to-action.jpg" alt="" />
                                </div>
                                <div className="col-lg-6 wow fadeIn" data-wow-delay="0.5s">
                                    <div className="mb-4">
                                        <h1 className="mb-3">Our Vision</h1>
                                        <p>To be a valuable real estate development company that benchmarks world-class real estate products and concepts, across all real estate verticals, thereby significantly contributing to building modern Tanzania and Creating greater Value for customers, corporate clients, and investors.</p>
                                    </div>
                                </div>



                                <div className="col-lg-6 wow fadeIn" data-wow-delay="0.5s">
                                    <div className="mb-4">
                                        <h1 className="mb-3">Our Mission</h1>
                                        <p>To be a valuable real estate development company that benchmarks world-class real estate products and concepts, across all real estate verticals, thereby significantly contributing to building modern Tanzania and Creating greater Value for customers, corporate clients, and investors.</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    )
}
