import React from "react";
import { NavLink, Link } from "react-router-dom";
import { useAuth } from "../../context/auth";
import useCategory from "../../hook/useCategory";

export const Header = () => {
  const [auth, setAuth] = useAuth();
  const categories = useCategory();

  const handleLogout = () => {
    setAuth({ user: null, token: "" });
    localStorage.removeItem("auth");
    alert("Your Logout");
  };

  return (
    <div className="container-fluid nav-bar bg-transparent">
      <nav className="navbar navbar-expand-lg bg-white navbar-light py-0 px-4">
        <NavLink
          to="/"
          className="navbar-brand d-flex align-items-center text-center"
        >
          <div className="icon p-2 me-2">
            <img
              className="img-fluid"
              src="img/icon-deal.png"
              alt="Icon"
              style={{ width: "30px", height: "30px" }}
            />
          </div>
          <h1 className="m-0 text-primary">Nitafutie</h1>
        </NavLink>
        <button
          type="button"
          className="navbar-toggler"
          data-bs-toggle="collapse"
          data-bs-target="#navbarCollapse"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarCollapse">
          <div className="navbar-nav ms-auto">
            <NavLink
              exact
              to="/"
              className="nav-item nav-link"
              activeClassName="active"
            >
              Home
            </NavLink>

            {/* <NavLink to="/property" className="nav-item nav-link">
              Property
            </NavLink> */}

            <div className="nav-item dropdown">
              <div
                className="nav-link dropdown-toggle"
                data-bs-toggle="dropdown"
              >
                Categories
              </div>
              <div className="dropdown-menu rounded-0 m-0">
                {categories?.map((c) => (
                  <Link to={`/categories/${c.slug}`} className="dropdown-item">
                    {c.name}
                  </Link>
                ))}
              </div>
            </div>

            {!auth?.user ? (
              <></>
            ) : (
              <div className="nav-item dropdown">
                <NavLink
                  to="/property"
                  className="nav-link dropdown-toggle"
                  data-bs-toggle="dropdown"
                >
                  Dashboard
                </NavLink>
                <div className="dropdown-menu rounded-0 m-0">
                  <NavLink
                    key="dashboard"
                    to={`/dashboard/${
                      auth?.user?.role === 1 ? "admin" : "user"
                    }`}
                    className="dropdown-item"
                  >
                    Dashboard
                  </NavLink>

                  <NavLink
                    onClick={handleLogout}
                    to="/"
                    className="dropdown-item"
                  >
                    Logout
                  </NavLink>
                </div>
              </div>
            )}

            <NavLink to="/login" className="nav-item nav-link">
              Login
            </NavLink>

            <NavLink to="/contact" className="nav-item nav-link">
              Contact
            </NavLink>
            <NavLink to="/about" className="nav-item nav-link">
              About
            </NavLink>
            <NavLink to="/terms" className="nav-item nav-link">
              Terms
            </NavLink>
          </div>
        </div>
      </nav>
    </div>
  );
};
