import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";

export const ForgotPassword = () => {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [badEmail, setBadEmail] = useState(false);
  const [newPassword, setnewPassword] = useState("");
  const [badnewPassword, setBadPassword] = useState(false);
  const [answer, setAnswer] = useState("");
  const [badAnswer, setBadAnswer] = useState(false);
  const handleSubmit = async (event) => {
    event.preventDefault();
    const isValid = validate();
    if (isValid) {
      const userData = {
        email,
        newPassword,
        answer,
      };
      try {
        const res = await axios.post("/api/v1/auth/forgot-password", userData);
        if (res && res.data.success) {
    
          navigate("/login");
        }
      } catch (error) {
        console.error("Error submitting form:", error);
      
      }
    } else {
      console.log("Form submission failed! Please check the fields.");
    }
  };

  const validate = () => {
    let isValid = true;
    if (!email) {
      setBadEmail(true);
      isValid = false;
    } else {
      setBadEmail(false);
    }
    if (!newPassword) {
      setBadPassword(true);
      isValid = false;
    } else {
      setBadPassword(false);
    }
    if (!answer) {
      setBadAnswer(true);
      isValid = false;
    } else {
      setBadAnswer(false);
    }

    return isValid;
  };

  return (
    <div className="col-sm-6 offset-3">
      <form className="form" onSubmit={handleSubmit}>
        <h3 className="text-center">Reset Password</h3>
        <div className="form-group">
          <label htmlFor="email">Email</label>
          <input
            type="email"
            id="email"
            name="email"
            className="form-control"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          {badEmail && <span className="text-danger">Email is required</span>}
        </div>
        <div className="form-group">
          <label htmlFor="password">New Password</label>
          <input
            type="password"
            id="password"
            name="password"
            className="form-control"
            value={newPassword}
            onChange={(e) => setnewPassword(e.target.value)}
          />
          {badnewPassword && (
            <span className="text-danger">Password is required</span>
          )}
        </div>

        <div className="form-group">
          <label htmlFor="phone">Security Answer</label>
          <input
            type="text"
            id="answer"
            name="answer"
            className="form-control"
            placeholder="What is your favorite food?"
            value={answer}
            onChange={(e) => setAnswer(e.target.value)}
          />
          {badAnswer && (
            <span className="text-danger">Answer the security question</span>
          )}
        </div>

        <div className="mb-3">
          <button type="submit" className="btn btn-primary">
            Update Password
          </button>
        </div>
      </form>
    </div>
  );
};
