import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { Layout } from "../../components/layouts/Layout";
import axios from "axios";
import { useParams } from "react-router-dom";
import { Prices } from "../../components/price";
import { Checkbox, Radio } from "antd";
export const Categories = () => {
  const params = useParams();
  const [posts, setPosts] = useState([]);
  const [category, setCategory] = useState([]);
  const [radio, setRadio] = useState([]);
  const [checked, setChecked] = useState([]);
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);
  const [categories, setCategories] = useState([]);
  //
  useEffect(() => {
    getAllCategories();
    getTotal();
  }, []);

  const getAllCategories = async () => {
    try {
      const { data } = await axios.get("/api/v1/auth/all-category");
      if (data?.success) {
        setCategories(data?.category);
      }
    } catch (error) {
      console.log("Error fetching categories:", error);
    }
  };

  const getTotal = async () => {
    try {
      const { data } = await axios.get("/api/v1/auth/post-count");
      if (data.success) {
        setTotal(data?.count);
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getAllPosts();
  }, []);

  const getAllPosts = async () => {
    try {
      setLoading(true);
      const { data } = await axios.get('/api/v1/auth/get-get-Plot/');
      setLoading(false);
      if (data.success) {
        setPosts(data.posts);
      }
    } catch (error) {
      setLoading(false);
      console.error("Error fetching posts:", error);
    }
  };

  const handleFilter = (value, id) => {
    let all = [...checked];
    if (value) {
      all.push(id);
    } else {
      all = all.filter((c) => c !== id);
    }
    setChecked([...all]);
  };

  useEffect(() => {
    if ( radio.length) getAllPosts();
  }, [radio.length]);

  useEffect(() => {
    if (checked.length || radio.length) searchPost();
  }, [checked, radio]);

  const searchPost = async () => {
    try {
      const { data } = await axios.post("/api/v1/auth/post-filters", {
        checked,
        radio,
      });
      if (data.success) {
        setPosts(data.posts);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleRadioChange = (e) => {
    setRadio(e.target.value);
  };

  const handleLoadMore = async () => {
    setLoading(true);
    try {
      const { data } = await axios.get(
        `/api/v1/auth/all-post?page=${page + 1}`
      );
      if (data.success) {
        setPosts([...posts, ...data.posts]);
        setPage(page + 1);
      }
    } catch (error) {
      console.error("Error loading more posts:", error);
    }
    setLoading(false);
  };

  useEffect(() => {
    if (loadMore === 1) return;
    loadMore();
  }, [page]);
  // load more post
  const loadMore = async () => {
    try {
      setLoading(true);
      const { data } = await axios.get(`/api/v1/auth/get-page/${page}`);
      setLoading(false);
      setPosts([...posts, ...data?.posts]);
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  useEffect(() => {
    const getPostByCategory = async () => {
      try {
        if (params?.slug) {
          const { data } = await axios.get(
            `/api/v1/auth/get-ByCategory/${params.slug}`
          );
          setPosts(data?.posts);
          setCategory(data?.category);
        }
      } catch (error) {
        console.log(error);
      }
    };

    getPostByCategory();
  }, [params?.slug]);
  // getby Price

  const formatNumber = (number) => {
    // Logic to format the number (e.g., add commas)
    return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  };
  return (
    <Layout>
      <div className="container-xxl bg-white p-0">
        <div
          className="container-fluid bg-primary mb-5 wow fadeIn"
          data-wow-delay="0.1s"
          style={{ padding: "35px", marginTop: "100px" }}
        ></div>

        <div className="text-center">
          <h3>Search By Price</h3>
          <div className="price">
            {Prices?.map((c) => (
              <Radio.Group
                key={c._id}
                onChange={handleRadioChange}
                value={radio}
              >
                <div key={c._id}>
                  <Radio value={c.array}> {formatNumber(c.name)} </Radio>
                </div>
              </Radio.Group>
            ))}
          </div>
        </div>
        <div className="tab-content">
          <div id="tab-1" className="tab-pane fade show p-0 active">
            <div className="row g-4">
              {posts.map((post) => (
                <div
                  key={post._id} // Add a unique key prop
                  className="col-lg-4 col-md-6 wow fadeInUp"
                  data-wow-delay="0.1s"
                >
                  <div className="property-item rounded overflow-hidden">
                    <div className="position-relative overflow-hidden">
                      <Link to={`/singlePost/${post.slug}`}>
                        <img
                          className="img-fluid"
                          src={`/api/v1/auth/get-photo/${post._id}`}
                          alt="Property"
                        />
                      </Link>
                      <div className="bg-primary rounded text-white position-absolute start-0 top-0 m-4 py-1 px-3">
                        {/* Whatever goes here */}
                      </div>
                      <div className="bg-white rounded-top text-primary position-absolute start-0 bottom-0 mx-4 pt-1 px-3">
                        {/* Whatever goes here */}
                      </div>
                    </div>
                    <div className="p-4 pb-0">
                      <h5 className="text-primary mb-3">Tsh{post.price}</h5>
                      <a className="d-block h5 mb-2" href="#">
                        {post.title}
                      </a>
                      <p>
                        <i className="fa fa-map-marker-alt text-primary me-2"></i>
                        {post.location}
                      </p>
                    </div>
                    <div className="d-flex border-top">
                      <small className="flex-fill text-center border-end py-2">
                        <i className="fa fa-ruler-combined text-primary me-2"></i>
                        {post.size} Sqft
                      </small>
                      <small className="flex-fill text-center border-end py-2">
                        <i className="fa fa-bed text-primary me-2"></i>
                        {post.bedrooms} Bed
                      </small>
                      <small className="flex-fill text-center py-2">
                        <i className="fa fa-bath text-primary me-2"></i>
                        {post.bathrooms} Bath
                      </small>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div class="col-12 text-center wow fadeInUp" data-wow-delay="0.1s">
            {posts && posts.length < total && (
              <button
                class="btn btn-primary py-3 px-5"
                onClick={(e) => {
                  e.preventDefault();
                  setPage(page + 1);
                }}
              >
                {loading ? "Loading..." : "Load More "}
              </button>
            )}
          </div>
      </div>
    </Layout>
  );
};
