import { Routes, Route } from "react-router-dom";
import "./App.css";
import "antd/dist/reset.css";
import { Home } from "./pages/home";
import { Register } from "./pages/register";
import { Login } from "./pages/login";
import { Contact } from "./pages/contact";
import { About } from "./pages/about";
import { Dashboard } from "./pages/owner/dashboard";
import Privateroutes from "./components/routes/private";
import { ForgotPassword } from "./pages/forgot-pass";
import AdminRoutes from "./components/routes/admin";
import { AdminDashboard } from "./pages/admin/admin";
import { Profile } from "./pages/owner/profile";
import { AllproPerty } from "./pages/owner/all-post";
import { Post } from "./pages/owner/post";
import { UpdatePost } from "./pages/owner/updatepost";
import { SinglePost } from "./pages/owner/singlePost";
import { Categories } from "./pages/owner/categories";
import { All,  } from "./pages/owner/property";
import { Search } from "./pages/owner/search";
import { Terms } from "./pages/terms";
import { GetAllUser } from "./pages/admin/all";
import { AllPost } from "./pages/admin/all-property";
function App() {
  return (
    <>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/register" element={<Register />} />
        <Route path="/search" element={<Search />} />
        <Route path="/terms" element={<Terms/>} />
        <Route path="/property" element={<All />} />
        <Route path="/categories/:slug" element={<Categories />} />
        <Route path="/login" element={<Login />} />
        <Route path="/contact" element={<Contact />} />
        <Route path="/about" element={<About />} />
        <Route path="/dashboard" element={<Privateroutes />}>
          <Route path="user" element={<Dashboard />} />
        </Route>
        {/* post middleware */}

        <Route path="/Profile" element={<Privateroutes />}>
          <Route path="" element={<Profile />} />
        </Route>

        {/* update post */}

        <Route path="/updatepost/:slug" element={<Privateroutes />}>
          <Route path="" element={<UpdatePost />} />
        </Route>
        {/* post nidlware */}

        <Route path="/post" element={<Privateroutes />}>
          <Route path="" element={<Post />} />
        </Route>
          <Route path="/singlePost/:slug" element={<SinglePost />} />
      
        <Route path="/all-post" element={<Privateroutes />}>
          <Route path="" element={<AllproPerty />} />
        </Route>
        <Route path="/dashboard" element={<AdminRoutes />}>
          <Route path="admin" element={<AdminDashboard />} />
        </Route>


        <Route path="/all" element={<AdminRoutes />}>
          <Route path="" element={<GetAllUser />} />
        </Route>

        <Route path="/all-property" element={<AdminRoutes />}>
          <Route path="" element={<AllPost />} />
        </Route>
        <Route path="/forgot-pass" element={<ForgotPassword />} />
      </Routes>
    </>
  );
}

export default App;
