import React, { useState } from "react";
import axios from "axios";
import { useAuth } from "../context/auth";

import { useNavigate, useLocation } from "react-router-dom";

export const Login = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [email, setEmail] = useState("");
  const [badEmail, setBadEmail] = useState(false);
  const [password, setPassword] = useState("");
  const [badPassword, setBadPassword] = useState(false);
  const [auth, setAuth] = useAuth();

  const handleSubmit = async (event) => {
    event.preventDefault();
    const isValid = validate();

    if (isValid) {
      const userData = {
        email,
        password,
      };

      try {
        const res = await axios.post("/api/v1/auth/login", userData);

        if (res && res.data.success) {
          setAuth({
            ...auth,
            user: res.data.user,
            token: res.data.token,
          });
          localStorage.setItem("auth", JSON.stringify(res.data));
          navigate(location.state || "/");
        }
      } catch (error) {
        console.error("Error submitting form:", error);
      }
    } else {
      console.log("Form submission failed! Please check the fields.");
    }
  };

  const validate = () => {
    let isValid = true;

    if (!email) {
      setBadEmail(true);
      isValid = false;
    } else {
      setBadEmail(false);
    }

    if (!password) {
      setBadPassword(true);
      isValid = false;
    } else {
      setBadPassword(false);
    }

    return isValid;
  };

  return (
    <div className="col-sm-6 offset-3">
      <form className="form" onSubmit={handleSubmit}>
        <h3 className="text-center">Login Nitafutie</h3>

        <div className="form-group">
          <label htmlFor="email">Email</label>
          <input
            type="email"
            id="email"
            name="email"
            className="form-control"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          {badEmail && <span className="text-danger">Email is required</span>}
        </div>

        <div className="form-group">
          <label htmlFor="password">Password</label>
          <input
            type="password"
            id="password"
            name="password"
            className="form-control"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          {badPassword && (
            <span className="text-danger">Password is required</span>
          )}
        </div>

        <div className="mb-3">
          <button type="submit" className="btn btn-primary">
            Login
          </button>
          
          <button
            className="btn btn-link"
            onClick={() => navigate("/forgot-pass")}
          >
            Forgot Password
          </button>

          <button
            className="btn btn-link"
            onClick={() => navigate("/register")}
          >
            Click here if you dont have an account
          </button>
        </div>
      </form>
    </div>
  );
};
