import React, { useEffect, useState } from "react";
import { Layout } from "../../components/layouts/Layout";
import axios from "axios";
import { useParams, Link } from "react-router-dom";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from "react-responsive-carousel";

export const SinglePost = () => {
  const params = useParams();
  const [post, setPost] = useState({});
  const [relatedPosts, setRelatedPosts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const { data } = await axios.get(
          `/api/v1/auth/get-singlePost/${params.slug}`
        );
        if (data.success) {
          setPost(data?.posts);
          getRelatedPosts(data?.posts._id, data?.posts.category?._id);
        }
      } catch (error) {
        console.error("Error fetching post:", error);
      } finally {
        setLoading(false);
      }
    };

    if (params?.slug) {
      fetchData();
    }
  }, [params?.slug]);

  const getRelatedPosts = async (postId, categoryId) => {
    try {
      const { data } = await axios.get(
        `/api/v1/auth/relate-post/${postId}/${categoryId}`
      );
      if (data.success) {
        setRelatedPosts(data?.posts);
      }
    } catch (error) {
      console.error("Error fetching related posts:", error);
    }
  };

  const formatNumber = (number) => {
    return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  };

  return (
    <Layout title="Nitafutie">
      <div className="container-xxl bg-white p-0">
        <div className="container-xxl py-5">
          <div className="container">
            <div className="tab-content">
              <div id="tab-1" className="tab-pane fade show p-0 active">
                {loading ? (
                  <div>Loading...</div>
                ) : (
                  <div className="row g-4">
                    {post && (
                      <>
                        <div
                          className="col-lg-6 col-md-6 wow fadeInUp"
                          data-wow-delay="0.1s"
                        >
                          <div className="property-item rounded overflow-hidden">
                            <Carousel>
                              {[1, 2, 3].map((item, index) => (
                                <div key={index}>
                                  <img
                                    className="img-fluid"
                                    src={`/api/v1/auth/get-photo/${post._id}`}
                                    alt={`Property ${index}`}
                                  />
                                </div>
                              ))}
                            </Carousel>
                            <div className="d-flex border-top">
                              <small className="flex-fill text-center border-end py-2">
                                <i className="fa fa-ruler-combined text-primary me-2"></i>
                                {post.size} Sqft
                              </small>
                              <small className="flex-fill text-center border-end py-2">
                                <i className="fa fa-bed text-primary me-2"></i>
                                {post.bedrooms} Bed
                              </small>
                              <small className="flex-fill text-center py-2">
                                <i className="fa fa-bath text-primary me-2"></i>
                                {post.bathrooms} Bath
                              </small>
                            </div>
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6 text-center">
                          <h3>Property Description</h3>
                          <p>Property Title: {post.title}</p>
                          <p>Location: {post.location}</p>
                          <p>Price: {formatNumber(post.price)}</p>
                          <p>Description: {post.description}</p>
                          <p>Category: {post?.category?.name}</p>
                          <p>phone: {post?.phone}</p>
                          <button className="btn btn-primary btn-block">
                            Continue 
                          </button>
                        </div>
                      </>
                    )}
                  </div>
                )}
              </div>
              <div className="col-sm-12">
                <h1 className="text-center">Related Posts</h1>
                <div className="row">
                  {relatedPosts.map((relatedPost) => (
                    <div key={relatedPost._id} className="col-md-4">
                      <div className="property-item rounded overflow-hidden">
                        <div className="position-relative overflow-hidden">
                          <Link to={`/singlePost/${relatedPost.slug}`}>
                            <img
                              className="img-fluid"
                              src={`/api/v1/auth/get-photo/${relatedPost._id}`}
                              alt="Related Property"
                            />
                          </Link>
                          {/* Additional content */}
                        </div>
                        <div className="p-4 pb-0">
                          <h5 className="text-primary mb-3">
                            Tsh{relatedPost.price}
                          </h5>
                          <Link
                            className="d-block h5 mb-2"
                            to={`/singlePost/${relatedPost.slug}`}
                          >
                            {relatedPost.title}
                          </Link>
                          <p>
                            <i className="fa fa-map-marker-alt text-primary me-2"></i>
                            {relatedPost.location}
                          </p>
                        </div>
                        <div className="d-flex border-top">
                          <small className="flex-fill text-center border-end py-2">
                            <i className="fa fa-ruler-combined text-primary me-2"></i>
                            {relatedPost.size} Sqft
                          </small>
                          <small className="flex-fill text-center border-end py-2">
                            <i className="fa fa-bed text-primary me-2"></i>
                            {relatedPost.bedrooms} Bed
                          </small>
                          <small className="flex-fill text-center py-2">
                            <i className="fa fa-bath text-primary me-2"></i>
                            {relatedPost.bathrooms} Bath
                          </small>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};
