import React from 'react';
import { Layout } from '../components/layouts/Layout';

export const Terms = () => {
    return (
        <Layout title={'Terms and Condition'}>
            <div className="container-xxl py-5">
                <div className="container">
                    <div className="text-center mx-auto mb-5 wow fadeInUp" data-wow-delay="0.1s" style={{ maxWidth: "600px" }}>
                        <h1 className="mb-3">Our Terms and Conditions</h1>
                        <p>Welcome to NITAFUTIE! Before you dive into exploring the exciting world of real estate on our platform, please take a moment to read and understand our terms and conditions. By accessing or using our website, you agree to comply with and be bound by the following terms. If you do not agree with these terms, please refrain from using our website.</p>
                        <h2>1. Acceptance of Terms</h2>
                        <p>By using our website, you acknowledge that you have read, understood, and agree to be bound by these terms and conditions. We reserve the right to update, modify, or change these terms at any time without prior notice. It is your responsibility to check this page periodically for changes.</p>
                        <h2>2. User Registration</h2>
                        <p>To access certain features of our website, you may be required to register an account. You agree to provide accurate, current, and complete information during the registration process and to update such information to keep it accurate, current, and complete. You are responsible for maintaining the confidentiality of your account and password and for restricting access to your computer.</p>
                        <h2>3. Content</h2>
                        <p>All content on this website, including but not limited to text, images, graphics, and logos, is the property of NITAFUTIE and is protected by copyright laws. You may not reproduce, distribute, display, or create derivative works from any of the content without our prior written permission.</p>
                        <h2>4. Listing and Property Information</h2>
                        <p>While we strive to provide accurate and up-to-date information, we do not guarantee the accuracy, completeness, or reliability of any property listings or other information on our website. Users are advised to independently verify the information provided before making any decisions based on it.</p>
                        <h2>5. User Conduct</h2>
                        <p>You agree not to use the website for any unlawful or prohibited purpose. This includes, but is not limited to, transmitting any harmful, threatening, abusive, harassing, defamatory, vulgar, obscene, or otherwise objectionable material.</p>
                        <h2>6. Privacy Policy</h2>
                    </div>
                </div>
            </div>
        </Layout>
    )
}
