import React from "react";
import { Layout } from "../../components/admin/Layouts";
import { useAuth } from "../../context/auth";
export const Profile = () => {
  const [auth, setAuth]=useAuth()
  return (
    <>
      <Layout>
        <section className="vh-100" style={{ backgroundColor: "#eee" }}>
          <div className="container py-5 h-100">
            <div className="row d-flex justify-content-center align-items-center h-100">
              <div className="col-ms-6  offset-3">
                <div className="card" style={{ borderRadius: "15px" }}>
                  <div className="card-body text-center">
                    <div className="mt-3 mb-4">
                      <img
                        src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-chat/ava2-bg.webp"
                        className="rounded-circle img-fluid"
                        style={{ width: "100px" }}
                        alt="Profile Avatar"
                      />
                    </div>
                    <h4 className="mb-2">Julie L. Arsenault</h4>
                    <p className="text-muted mb-4">
                      @Programmer <span className="mx-2">|</span>{" "}
                      <a href="#!">mdbootstrap.com</a>
                    </p>
           
                    <button
                      type="button"
                      className="btn btn-primary btn-rounded btn-lg"
                    >
                      Update Profile
                    </button>
                    <div className="d-flex justify-content-between text-center mt-5 mb-2">
                      <div>
                        <p className="mb-2 h5">8471</p>
                        <p className="text-muted mb-0">My Total Post</p>
                      </div>
                     
                      <div>
                        <p className="mb-2 h5">4751</p>
                        <p className="text-muted mb-0">Total Transactions</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </Layout>
    </>
  );
};
