import React, { useState } from "react";
import { Layout } from "../components/layouts/Layout";
import { useNavigate } from "react-router-dom";
import axios from 'axios';

export const Register = () => {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [badEmail, setBadEmail] = useState(false);
  const [password, setPassword] = useState("");
  const [badPassword, setBadPassword] = useState(false);
  const [confirmPassword, setConfirmPassword] = useState("");
  const [badConfirmPassword, setBadConfirmPassword] = useState(false);
  const [name, setName] = useState("");
  const [badName, setBadName] = useState(false);
  const [mobile, setMobile] = useState("");
  const [badMobile, setBadMobile] = useState(false);
  const [answer, setAnswer] = useState("");
  const [badAnswer, setBadAnswer] = useState(false);

  const validate = () => {
    let isValid = true;
    if (!email) {
      setBadEmail(true);
      isValid = false;
    } else {
      setBadEmail(false);
    }
    if (!password) {
      setBadPassword(true);
      isValid = false;
    } else {
      setBadPassword(false);
    }
    if (!confirmPassword || password !== confirmPassword) {
      setBadConfirmPassword(true);
      isValid = false;
    } else {
      setBadConfirmPassword(false);
    }
    if (!name) {
      setBadName(true);
      isValid = false;
    } else {
      setBadName(false);
    }
    if (!mobile) {
      setBadMobile(true);
      isValid = false;
    } else {
      setBadMobile(false);
    }
    if (!answer) {
      setBadAnswer(true);
      isValid = false;
    } else {
      setBadAnswer(false);
    }
    return isValid;
  }

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (validate()) {
      const userData = {
        name,
        email,
        mobile,
        password,
        confirmPassword,
        answer
      };
      try {
        const res = await axios.post('/api/v1/auth/register', userData);
        if (res && res.data.success) {
          console.log(res.data.message);
           alert('You Registered Successfuly Please Login') 
          setEmail("");
          setPassword("");
          setConfirmPassword("");
          setName("");
          setMobile("");
          setAnswer("");
          navigate('/login');
        } else {
          console.log("Registration failed:", res.data.message); // Log failure message
        }
      } catch (error) {
        console.error("Error submitting form:", error);
    
      }
    } else {
      console.log("Form submission failed! Please check the fields.");
    }
  }

  return (
    <Layout title={'Register'}>
      <div className="col-sm-6 offset-3">
        <form className="form" onSubmit={handleSubmit}>
          <h3 className="text-center">Register Nitafutie</h3>
          <div className="form-group">
            <label htmlFor="username">Username</label>
            <input type="text" id="username" name="username" className="form-control" value={name} onChange={(e) => setName(e.target.value)} />
            {badName && <span className="text-danger">Username is required</span>}
          </div>
          <div className="form-group">
            <label htmlFor="email">Email</label>
            <input type="email" id="email" name="email" className="form-control" value={email} onChange={(e) => setEmail(e.target.value)} />
            {badEmail && <span className="text-danger">Email is required</span>}
          </div>
          <div className="form-group">
            <label htmlFor="phone">Phone</label>
            <input type="tel" id="phone" name="phone" className="form-control" value={mobile} onChange={(e) => setMobile(e.target.value)} />
            {badMobile && <span className="text-danger">Phone number is required</span>}
          </div>
          <div className="form-group">
            <label htmlFor="password">Password</label>
            <input type="password" id="password" name="password" className="form-control" value={password} onChange={(e) => setPassword(e.target.value)} />
            {badPassword && <span className="text-danger">Password is required</span>}
          </div>
          <div className="form-group">
            <label htmlFor="confirmPassword">Confirm Password</label>
            <input type="password" id="confirmPassword" name="confirmPassword" className="form-control" value={confirmPassword} onChange={(e) => setConfirmPassword(e.target.value)} />
            {badConfirmPassword && <span className="text-danger">Passwords do not match</span>}
          </div>
          <div className="form-group">
            <label htmlFor="phone">Security Answer</label>
            <input type="text" id="answer"  name="answer" className="form-control" placeholder="What is your favorite food?" value={answer} onChange={(e) => setAnswer(e.target.value)} />
            {badAnswer && <span className="text-danger">Answer the security question</span>}
          </div>
          <button type="submit" className="btn btn-primary" disabled={badEmail || badPassword || badName || badMobile || badConfirmPassword || badAnswer}>Submit</button>
        </form>
      </div>
    </Layout>
  );
};
