import React, { useEffect, useState } from "react";
import { Layout } from "../../components/owner/Layouts";
import axios from "axios";
import { Modal } from "antd";
import { PostCategoryForm } from "../../components/form/form";
import { Link } from "react-router-dom";
export const AllPost = () => {
  const [posts, setPosts] = useState([]);

  const [users, setUsers] = useState([]);



  useEffect(() => {
    getAllUsers();
  }, []);

  const getAllUsers = async () => {
    try {
      const { data } = await axios.get("/api/v1/auth/all-users");
      if (data.success) {
        setUsers(data.users);
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getAllPosts();
  }, []);

  const getAllPosts = async () => {
    try {
      const { data } = await axios.get("/api/v1/auth/all-post");
      if (data.success) {
        setPosts(data.posts);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleDelete = async (postId) => {
    try {
      const { data } = await axios.delete(`/api/v1/auth/delete-post/${postId}`);
      if (data.success) {
        alert("Post deleted successfully");
        getAllPosts();
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Layout>
      <div className="content-wrapper">
        <section className="content-header">
          <div className="container-fluid">
            <div className="row mb-2">
              <div className="col-sm-6">
                <h1>All My Property</h1>
              </div>
              <div className="col-sm-6">
                <ol className="breadcrumb float-sm-right">
                  <li className="breadcrumb-item">
                    <a href="#">Home</a>
                  </li>
                  <li className="breadcrumb-item active">All My Property</li>
                </ol>
              </div>
            </div>
          </div>
        </section>

        <section className="content">
          <div className="card">
            <div className="card-body">
            <table id="example1" class="table table-bordered table-striped">
                <thead>
                  <tr>
                    <th>Property Title</th>
                    <th>Location</th>
                    <th>Price</th>
                    <th>Image</th>
                    <th>DateTime</th>
                    <th>User</th>
                    <th>Delete</th>
                  </tr>
                </thead>
                <tbody>
                  {posts.map((post) => (
                    <tr key={post._id}>
                      <td>{post.title}</td>
                      <td>{post.location}</td>
                      <td>{post.price}</td>
                      <td>
                        <img
                          src={`/api/v1/auth/get-photo/${post._id}`}
                          alt={post.title}
                          className="card-img-top"
                        />
                      </td>
                      <td>{post.createdAt.substring(0, 10)}</td>
                    <td>{post?.user?.username}</td>
                      <td>
                        <button
                          className="btn btn-danger"
                          onClick={() => handleDelete(post._id)}
                        >
                          Delete
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  );
};
